.public-index {
  .frontpage-section {
    position: relative;
    height: 100%;
    h2 {
      font-size: 4em;
      text-align: center;
      &.bold {
        font-weight: 700; }
      &.thin {
        font-weight: 200; } }
    p {
      font-size: 18px;
      line-height: 1.5em; } }

  // Header
  .frontpage-header {
    background: url('/images/shutterstock_152320880_tomato_hands.jpg') center center;
    background-size: cover;
    color: #fff;
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    a {
      color: #fff;
      width: 300px;
      margin-top: 50px; } }

