@media (min-width: 992px) {
  body.public {
    &.logged-in {
      .page {
        flex-direction: row;
        .content {
          width: calc(100% - 300px); } } } } }

body.public {
  .page {
    background: #f1f0ea;
    overflow: hidden;
    display: flex;
    align-items: stretch;
    flex-direction: column;

    .content {
      width: 100%;
      float: left;
      background: #f1f0ea;
      z-index: 0;

      .container-fluid {
        padding: 0; } }

    .top-container {
      margin-top: -200px; }

    .card {
      .card-header {
        font-weight: 700;
        color: #fff;
        background: #a9322a;
        border: 0; } }

    .header {
      #map {
        height: 500px;
        width: 100%;
        z-index: 0;
        border-bottom: 5px solid #e2d7b9; }
      .top {
        background: #a9322a;
        color: #fff;
        padding: 30px;
        h1 {
          margin: 0;
          font-weight: 700; }
        a {
          color: #fff; } } } } }

body.page {
  .page {
    .header {
      background: url('/images/co-own.jpg') center center;
      background-size: cover;
      color: #fff;
      height: 80vh;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      h2 {
        font-size: 4em;
        text-align: center;
        &.bold {
          font-weight: 700; }
        &.thin {
          font-weight: 200; } } }
    .top-container {
      .card {
        padding: 50px; } }
    .card {
      h2 {
        font-weight: bold;
        color: #666; }
      .body-text {
        font-size: 18px;
        line-height: 1.5em; } } } }

.public-pages-membership {
  .metrics {
    display: flex;
    justify-content: center;
    .metric {
      margin: 2vw;
      display: flex;
      flex-direction: column;
      align-items: center;
      .value {
        background: #efcb6b;
        color: #fff;
        font-size: 50px;
        width: 200px;
        line-height: 200px;
        border-radius: 100px;
        text-align: center;
        font-weight: bold; }
      .label {
        margin-top: 15px;
        font-weight: bold;
        font-size: 26px;
        line-height: 26px;
        color: #ad9350;
        text-align: center; } } } }
